function lastNews() {
    
    //   ()
    class TabItem {
        constructor(src, data, title, parentSelector, ...classes) {
            this.src = src;
            this.data = data;
            this.title = title;
            this.classes = classes;
            this.parent = document.querySelector(parentSelector);
        }
        convert() {
            const element = document.createElement('div');
            if (this.classes.length === 0) {
                this.element = 'tabItem';
                element.classList.add(this.element);
            } else {
                this.classes.forEach(className => element.classList.add(className));
            }
            
            element.innerHTML = `
                <div class="tabItem__pseudoButton">
                    <div>
                        <b class="read"></b>
                        <b class="active"></b>
                            
                    </div>
                </div>
                <img src=${this.src}>
                <div class="box">
                    <div class="box__data">
                        ${this.data}
                    </div>
                    <div class="box__descr">
                        ${this.title} 
                    </div>
                </div>
            `;
            this.parent.append(element);
        }
    }

    new TabItem(
        'images/tabs/s-tab.png',
        '15  2025',
        '     ',
        '.lastNews .tabcontainer'
    ).convert();

    new TabItem(
        'images/tabs/007-tab.png',
        '15  2025',
        '  .   ',
        '.lastNews .tabcontainer'
    ).convert();

    new TabItem(
        'images/tabs/third-tab.png',
        '15  2025',
        '       ',
        '.lastNews .tabcontainer'
    ).convert();

    new TabItem(
        'images/tabs/d-tab.png',
        '13  2025',
        ' , , !',
        '.lastNews .tabcontainer'
    ).convert();

    //   ( )
    class TabActive extends TabItem {
        constructor(src, data, view, title, descr, parentSelector, ...classes) {
            super(src, data, title, parentSelector, ...classes);
            this.view = view;
            this.descr = descr;
        }
        render() {
            const element = document.createElement('div');
            if (this.classes.length === 0) {
                this.element = 'tabcontent';
                element.classList.add(this.element);
            } else {
                this.classes.forEach(className => element.classList.add(className));
            }
            
            element.innerHTML = `
                <img class="tabActiveImg" src=${this.src}>
                <div class="box">
                    <div class="box__data">
                        <span>${this.data}</span>
                        <div class="i"><i class="fa-regular fa-eye"></i></div>
                        <span>${this.view}</span>
                    </div>
                    <div class="box__descr">
                        <div class="title">
                            ${this.title} 
                        </div>
                        <div class="descr">
                            ${this.descr}
                        </div>
                    </div>
                </div>
            `;
            this.parent.append(element);
        }
    }

    new TabActive(
        'images/tabs/tab-active1.png',
        '15  2025',
        '152',
        '     ',
        '          .        .  -     .             .',
        '.lastNews .lastNewsTabs'
    ).render();

    new TabActive(
        'images/tabs/tab-active2.png',
        '15  2025',
        '242',
        '  .   ',
        '         .     ,    ,        .',
        '.lastNews .lastNewsTabs'
    ).render();

    new TabActive(
        'images/tabs/tab-active3.png',
        '13  2025',
        '98',
        '       ',
        '      ,      .           ,        .',
        '.lastNews .lastNewsTabs'
    ).render();

    new TabActive(
        'images/tabs/tab-active4.png',
        '13  2025',
        '54',
        ' , , !',
        '                  .   ,  ,  ,           ,   .         ,       .         ,   ,       ,   .',
        '.lastNews .lastNewsTabs'
    ).render();
};
export default lastNews;