function boxOffice() {
    class boxOfficeCard {
        constructor(src, title, boxOffice, boxOfficeForWeeks, parentSelector, ...classes) {
            this.src = src;
            this.boxOffice = boxOffice;
            this.title = title;
            this.boxOfficeForWeeks = boxOfficeForWeeks;
            this.classes = classes;
            this.parent = document.querySelector(parentSelector);
        }
        render() {
            const element = document.createElement('div');
            if (this.classes.length === 0) {
                element.classList.add('filmBoxOffice__item');
            } else {
                this.classes.forEach(className => element.classList.add(className));
            }
            element.innerHTML = `
                <img src=${this.src}>
                <div class="filmBoxOffice__info">
                    <h6 class="title">
                        ${this.title}
                    </h6>
                    <div class="boxOffice">
                        ${this.boxOffice}
                    </div>
                    <div class="boxOfficeOnWeek">
                        ${this.boxOfficeForWeeks}
                    </div>
                </div>
            `;
            this.parent.append(element);
        }
    }

    //  

    new boxOfficeCard(
        'images/filmBoxOffice/holop.png',
        '1. ',
        '$10.8 ',
        '$13.2   4 ',
        '.expected .firstBoxOffice'
    ).render();

    new boxOfficeCard(
        'images/filmBoxOffice/sonic.png',
        '2.   ',
        '$5.1 ',
        '$15.1   4 ',
        '.expected .firstBoxOffice'
    ).render();

    new boxOfficeCard(
        'images/filmBoxOffice/bad-guys.png',
        '3.   ',
        '$4.9 ',
        '$6.2   3 ',
        '.expected .firstBoxOffice'
    ).render();

    new boxOfficeCard(
        'images/filmBoxOffice/forward.png',
        '4. ',
        '$3.7 ',
        '$21.4   4 ',
        '.expected .firstBoxOffice'
    ).render();

    new boxOfficeCard(
        'images/filmBoxOffice/ice.png',
        '5.  2',
        '$1.5 ',
        '$37.1   4 ',
        '.expected .firstBoxOffice'
    ).render();

    //  

    new boxOfficeCard(
        'images/filmBoxOffice/bloodShot.png',
        '1. ',
        '$13 ',
        '$15.1   4 ',
        '.expected .secondBoxOffice'
    ).render();

    new boxOfficeCard(
        'images/filmBoxOffice/forward.png',
        '2. ',
        '$6.8 ',
        '$41.4   4 ',
        '.expected .secondBoxOffice'
    ).render();

    new boxOfficeCard(
        'images/filmBoxOffice/invisibleMan.png',
        '3.  - ',
        '$6.2 ',
        '$58.3   3 ',
        '.expected .secondBoxOffice'
    ).render();

    new boxOfficeCard(
        'images/filmBoxOffice/sonic.png',
        '4.   ',
        '$2.9 ',
        '$15.1   4 ',
        '.expected .secondBoxOffice'
    ).render();

    new boxOfficeCard(
        'images/filmBoxOffice/gents.png',
        '5. ',
        '$1.8 ',
        '$78.7   11 ',
        '.expected .secondBoxOffice'
    ).render();

    //  

    new boxOfficeCard(
        'images/filmBoxOffice/joker.png',
        '1. ',
        '$17.2 ',
        '$91.5   4 ',
        '.expected .thirdBoxOffice'
    ).render();

    new boxOfficeCard(
        'images/filmBoxOffice/star-wars.png',
        '2.  ...',
        '$13.4 ',
        '$71.6   4 ',
        '.expected .thirdBoxOffice'
    ).render();

    new boxOfficeCard(
        'images/filmBoxOffice/bloodShot.png',
        '3. ',
        '$12 ',
        '$18.1   4 ',
        '.expected .thirdBoxOffice'
    ).render();

    new boxOfficeCard(
        'images/filmBoxOffice/sonic.png',
        '4.   ',
        '$10.9 ',
        '$45.1   4 ',
        '.expected .thirdBoxOffice'
    ).render();

    new boxOfficeCard(
        'images/filmBoxOffice/forward.png',
        '5. ',
        '$8.4 ',
        '$31.4   3 ',
        '.expected .thirdBoxOffice'
    ).render();
};
export default boxOffice;